import React from "react"
import s from "./Message.module.css"
import MyFriends from "../MyFriends/MyFriends";
import {BrowserRouter, NavLink, Route} from "react-router-dom";
import Dialogs from "../Dialogs/1";
import Dialogs2 from "../Dialogs/2";
import Dialogs3 from "../Dialogs/3";
import Dialogs4 from "../Dialogs/4";
import Dialogs5 from "../Dialogs/5";
import Dialogs6 from "../Dialogs/6";



const Message = () => {
    return (
        <BrowserRouter><div className={s.content}>
        <div className={s.contact}>
            <h3>Message</h3>
            <NavLink to="/dialogs/1"><MyFriends name={"Nikolay Iceman"} do={"нет новых сообщений"}/></NavLink>
            <NavLink to="/dialogs/2"><MyFriends name={"Mark Stavrovich"} do={"4 сообщения"}/></NavLink>
            <NavLink to="/dialogs/3"><MyFriends name={"Vladimir Putin"} do={"3 сообщения"}/></NavLink>
            <NavLink to="/dialogs/4"><MyFriends name={"Георгий Волосошвилли"} do={"3 сообщения"}/></NavLink>
            <NavLink to="/dialogs/5"><MyFriends name={"Vladimir Skoroportov"} do={"54 сообщения"}/></NavLink>
            <NavLink to="/dialogs/6"><MyFriends name={"Товаришь Главнокамандующий"} do={"10 сообщения"}/></NavLink>
        </div>
        <div className={s.dialogs}>
            <Route path="/dialogs/1" component={Dialogs}/>
            <Route path="/dialogs/2" component={Dialogs2}/>
            <Route path="/dialogs/3" component={Dialogs3}/>
            <Route path="/dialogs/4" component={Dialogs4}/>
            <Route path="/dialogs/5" component={Dialogs5}/>
            <Route path="/dialogs/6" component={Dialogs6}/>
            <div className={s.messageArea}>
                <input type="text" className={s.input}/>
                <input type="button" className={s.button}/>
            </div>

        </div>

    </div>
            </BrowserRouter>)
}
export default Message
